import React, { Component } from 'react'
import ImgsViewer from 'react-images-viewer'
import PropTypes from 'prop-types'
import { css, StyleSheet } from 'aphrodite/no-important'
import { Container, Row, Col } from 'react-bootstrap';

interface GalleryProps {
  imgs: any,
  heading: any,
  subheading: any,
  preventScroll: any,
  showThumbnails: any,
  spinner?: any,
  spinnerColor?: any,
  spinnerSize?: any,
  theme?: any
}

interface GalleryState {
  currImg: any,
  isOpen: boolean,

}

class Gallery extends Component<GalleryProps, GalleryState> {
  private displayName = 'Gallery';
  private propTypes = {
    preventScroll: PropTypes.bool,
    spinner: PropTypes.func,
    spinnerColor: PropTypes.string,
    spinnerSize: PropTypes.number,
    theme: PropTypes.object,
    heading: PropTypes.string,
    imgs: PropTypes.array,
    showThumbnails: PropTypes.bool,
    subheading: PropTypes.string,
  };

  constructor(props: GalleryProps) {
    super(props)

    this.state = {
      isOpen: false,
      currImg: 0,
    }

    this.gotoNext = this.gotoNext.bind(this)
    this.gotoPrev = this.gotoPrev.bind(this)
    this.gotoImg = this.gotoImg.bind(this)
    this.handleClickImg = this.handleClickImg.bind(this)
    this.closeImgsViewer = this.closeImgsViewer.bind(this)
    this.openImgsViewer = this.openImgsViewer.bind(this)
  }
  openImgsViewer(index, event) {
    event.preventDefault()
    this.setState({
      currImg: index,
      isOpen: true,
    })
  }
  closeImgsViewer() {
    this.setState({
      currImg: 0,
      isOpen: false,
    })
  }
  gotoPrev() {
    this.setState({
      currImg: this.state.currImg - 1
    })
  }
  gotoNext() {
    this.setState({
      currImg: this.state.currImg + 1
    })
  }
  gotoImg(index) {
    this.setState({
      currImg: index
    })
  }
  handleClickImg() {
    if (this.state.currImg === this.props.imgs.length - 1) return
    this.gotoNext()
  }

  renderGallery() {
    let imgs: any = this.props.imgs;

    if (!imgs) return <h1>no images</h1>;

    const gallery = imgs.filter(i => i).map((obj, i) => {
      return (
        <a
          href={obj.src}
          className={css(classes.thumbnail, classes[obj.orientation])}
          key={i}
          onClick={(e) => this.openImgsViewer(i, e)}
        >
            <img src={obj.src} className={css(classes.source)} />   
        </a>
      )
    }
    )


    return (
      <>
        <Row>
          <Col xs={4} md={4}>
          {gallery[0]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[1]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[2]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[3]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[4]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[5]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[6]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[7]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[8]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[9]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[10]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[11]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[12]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[13]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[14]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[15]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[16]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[17]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[18]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[19]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[20]}
          </Col>
        </Row>

        <Row>
          <Col xs={4} md={4}>
          {gallery[21]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[22]}
          </Col>
          <Col xs={4} md={4}>
          {gallery[23]}
          </Col>
        </Row>
     
      </>
    )
  }

  render() {
    return (
      <div>
        {this.props.heading && <h2>{this.props.heading}</h2>}
        {this.props.subheading && <p>{this.props.subheading}</p>}
        <Container>

          {this.renderGallery()}

        </Container>
        <ImgsViewer
          backdropCloseable
          currImg={this.state.currImg}
          imgs={this.props.imgs}
          isOpen={this.state.isOpen}
          onClickImg={this.handleClickImg}
          onClickNext={this.gotoNext}
          onClickPrev={this.gotoPrev}
          onClickThumbnail={this.gotoImg}
          onClose={this.closeImgsViewer}
          preventScroll={this.props.preventScroll}
          showThumbnails={this.props.showThumbnails}
          spinner={this.props.spinner}
          spinnerColor={this.props.spinnerColor}
          spinnerSize={this.props.spinnerSize}
          theme={this.props.theme}
        />
      </div>
    )
  }
}


const gutter = {
  small: 2,
  large: 4,
}
const classes = StyleSheet.create({
  gallery: {

    marginRight: -gutter.small,
    overflow: 'hidden',
    '@media (min-width: 500px)': {
      marginRight: -gutter.large,
    }

  },

  thumbnail: {
    boxSizing: 'border-box',
    display: 'block',
    float: 'left',
    lineHeight: 0,
    paddingRight: gutter.small,
    paddingBottom: gutter.small,
    overflow: 'hidden',

    '@media (min-width: 500px)': {
      paddingRight: gutter.large,
      paddingBottom: gutter.large,
    }
  },


  landscape: {
    width: '30%',
  },
  square: {
    paddingBottom: gutter.large,
    width: '40%',

    '@media (min-width: 500px)': {
      paddingBottom: gutter.large,
    }
  },


  source: {
    border: 0,
    display: 'inline',
    height: 'auto',
    maxWidth: '100%',
    width: 'auto'
  },
})

export default Gallery;